'use strict';

var Reflux = require('reflux');
var LocationsStore = require('./locationsStore');


var ProjectsStore = Reflux.createStore({

  init: function() {
    this.data = this.getInitialState();
    this.listenTo(LocationsStore, this.updateLocations);
  },

  updateLocations: function(locations) {
    this.data = locations.reduce(function(projects, location) {
      var id = location.properties.id;
      if (projects[id] === undefined) {
        projects[id] = {
          id: id,
          title: location.properties.title,
          sectors: location.properties.sectors,
          countryId: location.properties.countryId,
          locations: [location.properties.locationName],
        };
      } else {
        projects[id].locations.push(location.properties.locationName);
      }
      return projects;
    }, {});
    this.emit();
  },

  emit: function() {
    this.trigger(this.data);
  },

  getById: function(id) {
    return this.data[id];
  },

  getInitialState: function() {
    return [];
  },

});


module.exports = ProjectsStore;
