var React = require('react');


function stopClick(e) {
  e.stopPropagation();
  e.preventDefault();
}


var NotYet = React.createClass({displayName: "NotYet",
  render: function() {
    return (
      React.createElement("a", {
        onClick: stopClick, 
        title: "Not implemented for this demo", 
        href: "#", 
        className: 'not-ready ' + (this.props.className || '')}, 
        this.props.children
      )
    );
  }
});


module.exports = NotYet;
