'use strict';

var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;


var Legend = React.createClass({displayName: "Legend",
  render: function() {
    return (
      React.createElement("nav", {className: 'legend hidden legend-'+this.props.country}, 
        React.createElement("div", {className: "container-fluid"}, 
          this.props.children
        )
      )
    );
  },
});


Legend.Main = React.createClass({displayName: "Main",
  render: function() {
    return (
      React.createElement("ul", {className: "main"}, 
        this.props.children
      )
    );
  },
});


Legend.Item = React.createClass({displayName: "Item",
  render: function() {
    return (
      React.createElement("li", {className: (this.props.active ? 'active ' : '') +
                     (this.props.className ? this.props.className : '')}, 
        this.props.children
      )
    );
  },
});


module.exports = Legend;
